import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage, StaticImage } from "gatsby-plugin-image"
import { HiOutlineChevronDoubleDown } from "react-icons/hi";
import styled from '@emotion/styled'
import { keyframes } from '@emotion/react';

import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from 'gatsby-background-image'

import Box from '../../../components/Box'
import theme, { Media, responsive } from '../../../components/ThemeProvider/theme'

const GatsbyBgImage = styled(BackgroundImage)`
height: 100%;
width: 100%;
`

const Banner = () => {
  const { mobile, desktop } = useStaticQuery(
    graphql`
      query {
        mobile: file(relativePath: { eq: "containers/HomePage/Banner/mobile.jpg" }) {
          childImageSharp {
            gatsbyImageData
          }
        }
        desktop: file(relativePath: { eq: "containers/HomePage/Banner/desk.jpg" }) {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    `
  )
  const imageMobile = getImage(mobile)
  const bgImageMobile = convertToBgImage(imageMobile)
  const imageDesk = getImage(desktop)
  const bgImageDesk = convertToBgImage(imageDesk)

  const anime = keyframes`
    0%,,
    100% {
      transform: translateY(15%);
    }
    50% {
      transform: translateY(-15%);
    }
  `

  const DownAnimation = styled(Box)`
    animation: ${anime} 1s infinite;
  `

  return (
    <Box.Relative height="100%" width="auto">
      <Box as={Media} lessThan="tablet" height="100%">
        <GatsbyBgImage {...bgImageMobile} />
        {/* <Box.FullAbs>
          <StaticImage src="mobile.jpg" alt="越永續，越得意" />
        </Box.FullAbs> */}
        <Box.FullAbs>
          <StaticImage src="mobile_text.png" alt="越永續，越得意" />
        </Box.FullAbs>
      </Box>
      <Box as={Media} greaterThanOrEqual="tablet" height="100%">
        <GatsbyBgImage {...bgImageDesk} />
      </Box>
      <Box.Absolute left="50%" transform="translateX(-50%)" bottom={responsive('1em', '3em')} color="white">
        <DownAnimation>
          <Media lessThan="tablet">
            <HiOutlineChevronDoubleDown size="2em" />
          </Media>
          <Media greaterThanOrEqual="tablet">
            <HiOutlineChevronDoubleDown size="2.5em" />
          </Media>
        </DownAnimation>
      </Box.Absolute>
    </Box.Relative>
  )
}

export default Banner
